import { Router } from 'express';
import {
  getAll,
  getOne,
  createOne,
  updateOne,
  pagination,
} from './commerceRiskParam.controller';

const router = Router();

router.get('/v1/commerceRiskParam', getAll);

router.post('/v1/commerceRiskParam', createOne);

router.get('/v1/commerceRiskParam/pagination/:page/:perPage', pagination);

router.get('/v1/commerceRiskParam/:_id', getOne);

router.put('/v1/commerceRiskParam/:_id', updateOne);

export default router;
